/* eslint-disable @typescript-eslint/no-var-requires */
// react
import React, { useEffect, useRef, useState } from "react";
// mui
import { Button } from "@mui/material";
// style
import styles from "./UnityCanvas.module.scss";
// hooks
import { useUnity } from "../hooks/useUnity";
// components
import { socket } from "../util/SocketProvider";
// external json
import unityBinaryData from "../unityBinaryData.json";
import { SoraProvider } from "../util/SoraProvider";
import { ConnectionPublisher, DataChannelEvent, DataChannelMessageEvent } from "sora-js-sdk";

const HelpPageURL = "https://dev.xrccg.com:4004/faq";

type Props = {
    width?: number;
    height?: number;
};

type SendUserTokenType = {
    token: string;
};

type SendRtcSettingsType = {
    settings: string;
};

export type RtcSettings = {
    service_type: string;
    audio_code_type: string;
    video_code_type: string;
    video_resolution_width: number;
    video_resolution_height: number;
    video_fps_max: number;
    video_fps_min: number;
};

type NotificationMessage = {
    type: string;
    method: string;
    peer_id: string;
    from_id: string;
};

type ResponseMessage = {
    method: string;
    result: string;
    message: string;
    sora_access_token: SoraAccessToken;
    channel_id: string;
};

type SoraAccessToken = {
    access_token: string;
};

type SetVerticalLayoutFlagType = {
    verticalFlag: boolean;
};

type StartListeningType = {
    language: string;
    continuous: boolean;
};

type LoginAccessGroup = {
    group: string;
    history: string;
};

const unityBuildRoot = "./Build";
export let unityInstanceRef: React.MutableRefObject<UnityInstance | undefined>;
let unityContainerRef: React.MutableRefObject<HTMLDivElement | null>;

let isStartRecognition = false;
let continuous = false;
let finalTranscript = "";

export const UnityCanvas = ({ width, height }: Props) => {
    const [fullscreenBtnEnabled, setFullscreenBtnEnabled] = useState(false);
    const [userToken, setUserToken] = useState("");
    const [rtcSettings, setRtcSettings] = useState<RtcSettings>();
    const [sora, setSora] = useState<ConnectionPublisher>();
    const [renderCount, setRenderCount] = useState(0);
    const [helpButtonToggle, setHelpButtonToggle] = useState(false);
    const remoteAudioRef = useRef<HTMLAudioElement>(null);
    const [verticalFlag, setVerticalFlag] = useState(false);

    // コンポーネント再読み込み
    const renderComponent = () => {
        // 再レンダリング用のステート更新
        console.log("UnityCanvas 再レンダリング");
        setRenderCount((prevCount) => prevCount + 1);
        // フルスクリーンボタン有効化
        setFullscreenBtnEnabled(false);
    };

    // キャンバスサイズ調整
    const resizeCanvas = (canvasElement: HTMLCanvasElement | null | undefined, verticalFlag: boolean) => {
        console.log(`resizeCanvas! verticalFlag = ${verticalFlag}`);
        if (canvasElement) {
            const aspectRatio = 9 / 16;
            let canvasWidth = 0;
            let canvasHeight = 0;
            if (verticalFlag) {
                canvasHeight = window.innerHeight * 0.9;
                canvasWidth = canvasHeight * aspectRatio;
            } else {
                canvasWidth = window.innerWidth * 0.9;
                canvasHeight = canvasWidth * aspectRatio;
            }
            canvasElement.style.width = canvasWidth.toString() + "px";
            canvasElement.style.height = canvasHeight.toString() + "px";
            console.log(`canvas width = ${canvasElement.style.width}, height = ${canvasElement.style.height}`);
        }
    };

    // Unityインスタンス生成
    const canvas = window.document.createElement("canvas");
    const { instanceRef, containerRef } = useUnity({
        canvas,
        unityBuildRoot,
        renderCount,
    });
    unityInstanceRef = instanceRef;
    unityContainerRef = containerRef;

    // Unityスクリプト読み込み
    useEffect(() => {
        // helpパラメータ指定時はボタン表示
        const url = new URL(window.location.href);
        const params = url.searchParams;
        const help = params.get("help");
        if (help != null && help == "enable") {
            setHelpButtonToggle(true);
        }

        // Unityスクリプト読み込み
        const scriptSrc = `${unityBuildRoot}/${unityBinaryData.loaderUrl}`;
        const root = window.document.getElementById("root");
        const scriptTag = window.document.createElement("script");
        scriptTag.type = "text/javascript";
        scriptTag.src = scriptSrc;
        if (root) {
            root.appendChild(scriptTag);
        }

        // Unity初期化完了
        const gameControllerInitializedHandler = () => {
            // フルスクリーンボタン有効化
            setFullscreenBtnEnabled(true);
        };

        // Unity初期化完了イベント購読
        window.addEventListener("gamecontroller_initialized", gameControllerInitializedHandler);

        // 音声認識初期化
        const speechRecognition = window.webkitSpeechRecognition || window.SpeechRecognition;
        if ('webkitSpeechRecognition' in window) {
            // ブラウザ対応
            console.log("Web Speech API対応ブラウザです");
        } else {
            // ブラウザ非対応
            console.error("Web Speech API非対応ブラウザです");
        }
        const recognition = new speechRecognition();
        recognition.onresult = (event) => {
            console.log(`recognition onresult start: count = ${event.results.length}`);
            for (let i = event.resultIndex; i < event.results.length; i++) {
                const results = event.results;
                const transcript = event.results[i][0].transcript; 
                if (results[i].isFinal) {
                    // 音声認識文字列を保持
                    finalTranscript += transcript;
                    console.log(`音声認識終了: transcript = ${transcript}, finalTranscript = ${finalTranscript}`);
                    // 音声認識完了時の最終文字列をUnity側に送信
                    if (unityInstanceRef.current !== undefined) {
                        unityInstanceRef.current.SendMessage("GameManager", "SetSpeakingTranscriptText", finalTranscript);
                    }
                } else {
                    console.log(`音声認識中: transcript = ${transcript}`);
                    // 音声認識中の経過文字列をUnity側に送信
                    if (unityInstanceRef.current !== undefined) {
                        unityInstanceRef.current.SendMessage("GameManager", "SetSpeakingTranscriptText", transcript);
                    }
                }
            }
            console.log("recognition onresult end");
        }
        recognition.onend = () => {
            console.log(`onend: finalTranscript = ${finalTranscript}`);
            // 音声認識完了時の最終文字列をUnity側に送信
            if (finalTranscript != "") {
                if (unityInstanceRef.current !== undefined) {
                    const temp = finalTranscript;
                    finalTranscript = "";
                    unityInstanceRef.current.SendMessage("GameManager", "SetCompleteTranscriptText", temp);
                }
            }
        }
        recognition.onerror = () => {
            console.error("recognition onerror");
        }
        recognition.onnomatch = () => {
            console.error("recognition onnomatch");
        }

        // 音声認識開始
        const startSpeechRecognitionHandler = (ev: CustomEvent<StartListeningType>) => {
            if (isStartRecognition) return;
            const languageCode = getLanguageCode(ev.detail.language);
            continuous = ev.detail.continuous;
            console.log("startListening: language code = ", languageCode, " continuous = ", continuous);
            recognition.lang = languageCode;
            recognition.continuous = continuous;
            recognition.interimResults = false;
            recognition.start();
            isStartRecognition = true;
        };
        // 音声認識終了
        const stopSpeechRecognitionHandler = () => {
            if (!isStartRecognition) return;
            isStartRecognition = false;
            recognition.stop();
        };

        // 音声認識開始イベント購読
        window.addEventListener("speechrecognition_start", startSpeechRecognitionHandler as EventListenerOrEventListenerObject);
        // 音声認識終了イベント購読
        window.addEventListener("speechrecognition_end", stopSpeechRecognitionHandler);

        const getLanguageCode = (languageStr: string): string => {
            languageStr = languageStr.toLowerCase();
            switch (languageStr) {
                case "japanese":
                    return "ja-JP";
                case "english":
                    return "en-US";
                case "simplifiedchinese":
                case "traditionalchinese":
                    return "zh-CN";
                case "korean":
                    return "ko-KR";
                case "russian":
                    return "ru-RU";
                case "spanish":
                    return "es-ES";
                case "arabic":
                    return "ar-SA";
                case "vietnamese":
                    return "vi-VN";
                default:
                    return "ja-JP";
            }
        };

        return () => {
            // Unity初期化完了イベント購読解除
            window.removeEventListener("gamecontroller_initialized", gameControllerInitializedHandler);
            // 音声認識開始イベント購読解除
            window.removeEventListener("speechrecognition_start", startSpeechRecognitionHandler as EventListenerOrEventListenerObject);
            // 音声認識終了イベント購読解除
            window.removeEventListener("speechrecognition_end", stopSpeechRecognitionHandler);
        };
    }, [renderCount]);

    // トークン更新時の処理
    useEffect(() => {
        // シグナリングサーバと接続された
        const onConnect = () => {
            // console.log("onConnect");
            if (unityInstanceRef.current !== undefined) {
                unityInstanceRef.current.SendMessage("GameManager", "SignalingServerOnConnect");
            }
        };

        // シグナリングサーバから切断された
        const onDisconnect = () => {
            // console.log("onDisconnect");
            if (unityInstanceRef.current !== undefined) {
                unityInstanceRef.current.SendMessage("GameManager", "SignalingServerOnDisconnect");
            }
        };

        // シグナリングサーバからエラー受信
        const onError = (error: string) => {
            console.log("onError: ", error);
        };

        // シグナリングサーバから通知受信
        const onNotification = (message: NotificationMessage) => {
            if (message.type != undefined) {
                console.log(`onNotification: type = ${message.type}`);
            } else if (message.method != undefined) {
                console.log(`onNotification: method = ${message.method}`);
            }
            switch (message.method) {
                case "getCallingUser": {
                    // チャネルIDとアクセストークンを取得
                    const peerID = message.peer_id;
                    if (peerID == null || peerID == undefined) {
                        console.error("peerID is null or undefined!");
                    } else {
                        const channelID = peerID.split(":")[0];
                        const accessToken = peerID.split(":")[1];
                        console.log(`channelID = ${channelID}, accessToken = ${accessToken}`);
                        ConnectSora(channelID, accessToken);
                    }
                    break;
                }
            }
            if (unityInstanceRef.current !== undefined) {
                unityInstanceRef.current.SendMessage("GameManager", "SignalingServerOnNotification", JSON.stringify(message));
            }
        };

        // シグナリングサーバからレスポンス受信
        const onResponse = async (message: ResponseMessage) => {
            console.log(`onResponse: method = ${message.method}, result = ${message.result}`);
            // Unityにメッセージ送信
            if (unityInstanceRef.current !== undefined) {
                unityInstanceRef.current.SendMessage("GameManager", "SignalingServerOnResponse", JSON.stringify(message));
            }
        };

        // ユーザートークン受信時ハンドラ
        const receivedTokenHandler = (ev: CustomEvent<SendUserTokenType>) => {
            setUserToken(ev.detail.token);
        };

        // RTC設定受信時ハンドラ
        const receivedRtcSettingsHandler = (ev: CustomEvent<SendRtcSettingsType>) => {
            setRtcSettings(JSON.parse(ev.detail.settings));
        };

        const addSocketEvent = () => {
            socket.on("connect", onConnect);
            socket.on("disconnect", onDisconnect);
            socket.on("error", onError);
            socket.on("notification", onNotification);
            socket.on("response", onResponse);
        };

        const removeSocketEvent = () => {
            socket.off("connect", onConnect);
            socket.off("disconnect", onDisconnect);
            socket.off("error", onError);
            socket.off("notification", onNotification);
            socket.off("response", onResponse);
        };

        // シグナリングサーバ接続開始時ハンドラ
        const connectHandler = () => {
            // console.log("connectHandler");
            // socket.io接続
            socket.connect();
        };

        // シグナリングサーバ接続終了時ハンドラ
        const disconnectHandler = () => {
            // console.log("disconnectHandler");
            // socket.io切断
            socket.disconnect();
        };

        // シグナリングサーバログイン時ハンドラ
        const loginHandler = (ev: CustomEvent<LoginAccessGroup>) => {
            const value = userToken + "," + "sora" + "," + "calling" + "," + ev.detail.group + "," + ev.detail.history;
            console.log("loginUser value = ", value);
            socket.emit("loginUser", value);
        };

        const WebRTCDisconnect = async () => {
            if (sora == null || sora == undefined) {
                console.log("WebRTCDisconnect error: sora is null or undefined");
            } else {
                sora?.disconnect();
                console.log("WebRTCDisconnect completed");
            }
        };

        const ConnectSora = async (channelID: string, accessToken: string) => {
            console.log("ConnectSora!!!");
            try {
                // サポートされている制約を取得
                const supports = navigator.mediaDevices.getSupportedConstraints();
                console.log(
                    `browser support constraints: width = ${supports["width"]}, height = ${supports["height"]}, frameRate = ${supports["frameRate"]}`
                );
                // カメラデバイスからストリームを取得
                const videoSettingLog =
                    rtcSettings?.video_code_type == "non"
                        ? "rtcSettings: video codec type = non"
                        : `rtcSettings: video codec type = ${rtcSettings?.video_code_type}, width = ${rtcSettings?.video_resolution_width}, height = ${rtcSettings?.video_resolution_height}, fps max = ${rtcSettings?.video_fps_max}, min = ${rtcSettings?.video_fps_min}`;
                console.log(`${videoSettingLog}`);
                //! サーバのビデオオプション実装後に書き換え
                const stream = await navigator.mediaDevices.getUserMedia({
                    audio: true,
                    video:
                        rtcSettings?.video_code_type == "non"
                            ? false
                            : {
                                  width: { ideal: rtcSettings?.video_resolution_width },
                                  height: { ideal: rtcSettings?.video_resolution_height },
                                  frameRate: { max: rtcSettings?.video_fps_max, min: rtcSettings?.video_fps_min },
                              },
                });
                // Sora初期化
                const sendrecv = SoraProvider({ channelID: channelID, accessToken: accessToken, rtcSettings });
                // console.log(`channel id = ${channelID}, access token = ${soraAccessToken}`);
                sendrecv.on("track", (event) => {
                    const stream = event.streams[0];
                    // console.log("sora track");
                    if (!stream) {
                        console.log(`on track error!`);
                        return;
                    }
                    if (event.track.kind === "video") {
                        // 接続相手のカメラを描画
                        console.log(`Add mediastream video track: ${stream.id}`);
                    } else if (event.track.kind === "audio") {
                        console.log(`Add mediastream audio track: ${stream.id}`);
                        if (remoteAudioRef.current) {
                            remoteAudioRef.current.style.border = "1px solid red";
                            remoteAudioRef.current.autoplay = true;
                            remoteAudioRef.current.controls = false;
                            remoteAudioRef.current.srcObject = stream;
                        }
                    }
                    if (unityInstanceRef.current !== undefined) {
                        unityInstanceRef.current.SendMessage("GameManager", "WebRTCConnect");
                    }
                });
                sendrecv.on("removetrack", (event) => {
                    console.log("sora removetrack");
                    if (remoteAudioRef.current) {
                        remoteAudioRef.current.srcObject = null;
                    }
                    if (unityInstanceRef.current !== undefined) {
                        unityInstanceRef.current.SendMessage("GameManager", "WebRTCDisconnect");
                    }
                    sendrecv.removeVideoTrack(stream);
                    sendrecv.removeAudioTrack(stream);
                    sendrecv.disconnect();
                });
                sendrecv.on("datachannel", (event: DataChannelEvent) => {
                    // ここにデータチャネルが利用可能になった際の処理を書く
                    const label = event.datachannel.label;
                    const direction = event.datachannel.direction;
                    // console.log(`sora datachannel: label = ${label}, direction = ${direction}`);
                });
                sendrecv.on("message", async (event: DataChannelMessageEvent) => {
                    // ここでデータチャネルメッセージイベントを処理する
                    const label = event.label;
                    const data = event.data;
                    // console.log(`sora message: label = ${label}, data length = ${data.byteLength}`);
                    if (unityInstanceRef.current !== undefined) {
                        const message = new TextDecoder().decode(data);
                        switch (label) {
                            case "#systemCall":
                                // システムコール受信
                                {
                                    console.log(`received system call: message = ${message}`);
                                    switch (message) {
                                        case "enable camera":
                                            // Webカメラ映像を送信
                                            {
                                                if (rtcSettings?.video_code_type != "non") {
                                                    // カメラデバイスからストリームを取得
                                                    const cameraStream = await navigator.mediaDevices.getUserMedia({
                                                        audio: true,
                                                        video: true,
                                                    });
                                                    sendrecv.replaceVideoTrack(stream, cameraStream.getVideoTracks()[0]);
                                                }
                                            }
                                            break;
                                        case "enable capture":
                                            // アプリ画面を送信
                                            {
                                                if (rtcSettings?.video_code_type != "non") {
                                                    // キャンバスからストリーム取得
                                                    const canvasElement = unityContainerRef.current?.querySelector("canvas");
                                                    if (canvasElement == null || canvasElement == undefined) {
                                                        console.error("canvas element is invalid");
                                                        return;
                                                    }
                                                    const canvasStream = canvasElement.captureStream();
                                                    const canvasVideoTrack = canvasStream.getVideoTracks()[0];
                                                    sendrecv.replaceVideoTrack(stream, canvasVideoTrack);
                                                }
                                            }
                                            break;
                                        default:
                                            // Unity側で処理
                                            unityInstanceRef.current.SendMessage("GameManager", "ReceivedSystemCall", message);
                                            break;
                                    }
                                }
                                break;
                            case "#documentImage":
                                {
                                    // 資料画像ファイル名受信
                                    const fileName = message;
                                    unityInstanceRef.current.SendMessage("GameManager", "ReceivedDocumentFileName", fileName);
                                }
                                break;
                            case "#faceInfo":
                                // 顔情報受信
                                unityInstanceRef.current.SendMessage("GameManager", "ReceivedFaceInfo", message);
                                break;
                            case "#voiceVolume":
                                {
                                    // 通話相手の音声音量受信
                                    const vol = parseFloat(message);
                                    unityInstanceRef.current.SendMessage("GameManager", "ReceivedVoiceVolume", vol);
                                }
                                break;
                            case "#inputText":
                                {
                                    // 音声ファイル名受信
                                    const fileName = message;
                                    unityInstanceRef.current.SendMessage("GameManager", "ReceivedInputTextFileName", fileName);
                                }
                                break;
                            default:
                                console.error(`Invalid datachannel message received.`);
                                break;
                        }
                    }
                });
                // Sora接続
                await sendrecv.connect(stream);
                // Sora保持
                setSora(sendrecv);
            } catch (err) {
                console.error("ConnectSora error: " + err);
            }
        };

        const sendOrientationTypeHandler = (ev: CustomEvent<SetVerticalLayoutFlagType>) => {
            console.log("sendOrientationTypeHandler: flag = ", ev.detail.verticalFlag);
            const canvasElement = unityContainerRef.current?.querySelector("canvas");
            resizeCanvas(canvasElement, ev.detail.verticalFlag);
            // ビューのリフレッシュが終わるまで待機する必要がある
            if (unityInstanceRef.current !== undefined) {
                unityInstanceRef.current.SendMessage("GameManager", "SetDelayForOrientation", "1000");
            }
            setVerticalFlag(ev.detail.verticalFlag);
        };

        const logout = () => {
            if (unityInstanceRef.current !== undefined) {
                unityInstanceRef.current.SendMessage("GameManager", "Logout");
            }
        };

        // ページ終了直前の処理
        const handleBeforeUnload = () => {
            console.log("beforeunload");
            // sora切断
            WebRTCDisconnect();
            // ログアウト
            logout();
        };

        const handleResizeWindow = () => {
            console.log(`handleResizeWindow`);
            const canvasElement = unityContainerRef.current?.querySelector("canvas");
            resizeCanvas(canvasElement, verticalFlag);
        };

        // ソケットイベント追加
        addSocketEvent();
        // ユーザートークン取得イベント購読
        window.addEventListener("send_user_token", receivedTokenHandler as EventListenerOrEventListenerObject);
        // RTC設定取得イベント購読
        window.addEventListener("send_rtc_settings", receivedRtcSettingsHandler as EventListenerOrEventListenerObject);
        // 縦横タイプイベント購読
        window.addEventListener("send_orientationtype", sendOrientationTypeHandler as EventListenerOrEventListenerObject);
        // シグナリングサーバ接続開始イベント購読
        window.addEventListener("signaling_connect", connectHandler);
        // シグナリングサーバ接続終了イベント購読
        window.addEventListener("signaling_disconnect", disconnectHandler);
        // シグナリングサーバログインイベント購読
        window.addEventListener("signaling_login", loginHandler as EventListenerOrEventListenerObject);
        // WebRTC接続切断イベント購読
        window.addEventListener("webrtc_disconnect", WebRTCDisconnect);
        // ページ終了直前イベント購読
        window.addEventListener("beforeunload", handleBeforeUnload);
        // コンポーネント再読み込みイベント購読
        window.addEventListener("reload_unitycomponent", renderComponent);
        // ブラウザリサイズイベント購読
        window.addEventListener("resize", handleResizeWindow);

        return () => {
            // ソケットイベント解除
            removeSocketEvent();
            // ユーザートークン取得イベント購読解除
            window.removeEventListener("send_user_token", receivedTokenHandler as EventListenerOrEventListenerObject);
            // RTC設定取得イベント購読解除
            window.removeEventListener("send_rtc_settings", receivedRtcSettingsHandler as EventListenerOrEventListenerObject);
            // 縦横タイプイベント購読
            window.removeEventListener("send_orientationtype", sendOrientationTypeHandler as EventListenerOrEventListenerObject);
            // シグナリングサーバ接続開始イベント購読解除
            window.removeEventListener("signaling_connect", connectHandler);
            // シグナリングサーバ接続終了イベント購読解除
            window.removeEventListener("signaling_disconnect", disconnectHandler);
            // シグナリングサーバログインイベント購読解除
            window.removeEventListener("signaling_login", loginHandler as EventListenerOrEventListenerObject);
            // WebRTC接続切断イベント購読解除
            window.removeEventListener("webrtc_disconnect", WebRTCDisconnect);
            // ページ終了直前イベント購読解除
            window.removeEventListener("beforeunload", handleBeforeUnload);
            // コンポーネント再読み込みイベント購読解除
            window.removeEventListener("reload_unitycomponent", renderComponent);
            // ブラウザリサイズイベント購読解除
            window.removeEventListener("resize", handleResizeWindow);
        };
    }, [userToken, sora, rtcSettings, renderCount]);

    // フルスクリーンボタン設定
    const ClickFullscreenBtn = () => {
        unityInstanceRef.current?.SetFullscreen(true);
    };

    const openLicenseFile = async () => {
        const url = process.env.PUBLIC_URL + "/license.txt";
        try {
            const response = await fetch(url);
            const text = await response.text();
            // 別タブで開く
            const newTab = window.open(url, "_blank");
            if (newTab) {
                newTab.document.open();
                // 文字コードをUTF-8に設定
                newTab.document.writeln(`<meta charset="UTF-8">`);
                // license.txt の内容を表示
                newTab.document.writeln(`<pre>${text}</pre>`);
                newTab.document.close();
                /*
                // 文字コードをUTF-8に設定
                const metaCharset = newTab.document.createElement("meta");
                metaCharset.setAttribute("charset", "UTF-8");
                newTab.document.head?.appendChild(metaCharset);
                // license.txt の内容を表示
                const preElement = newTab.document.createElement("pre");
                preElement.textContent = text;
                newTab.document.body?.appendChild(preElement);
                */
            }
        } catch (error) {
            console.error("openLicenseFile fetch error:", error);
        }
    };

    const openHelpPage = async () => {
        try {
            window.open(HelpPageURL, "_blank");
        } catch (error) {
            console.error("openHelpPage fetch error:", error);
        }
    };

    return (
        <>
            <div className={styles.canvas_wrapper}>
                <div className={styles.canvas} ref={unityContainerRef} />
            </div>
            <div className={styles.button_area}>
                <Button
                    className={styles.button}
                    variant="contained"
                    disabled={!fullscreenBtnEnabled}
                    onClick={ClickFullscreenBtn}
                    sx={{ bgcolor: "#4D8E8A", "&:hover": { bgcolor: "#3A827E" } }}
                >
                    フルスクリーン
                </Button>
                <Button
                    className={styles.button}
                    variant="contained"
                    onClick={openLicenseFile}
                    sx={{ bgcolor: "#4D8E8A", "&:hover": { bgcolor: "#3A827E" } }}
                >
                    ライセンス
                </Button>
                <Button
                    className={styles.button}
                    variant="contained"
                    onClick={openHelpPage}
                    sx={{ display: helpButtonToggle ? "inherit" : "none", bgcolor: "#4D8E8A", "&:hover": { bgcolor: "#3A827E" } }}
                >
                    ヘルプ
                </Button>
                <audio ref={remoteAudioRef}>Remote Audio</audio>
            </div>
        </>
    );
};
