import { useEffect, useRef, useState } from "react";
import unityBinaryData from "../unityBinaryData.json";

type Props = {
    canvas: HTMLCanvasElement;
    unityBuildRoot: string;
    renderCount: number;
};

export const useUnity = ({ canvas, unityBuildRoot, renderCount }: Props) => {
    const [retryCount, setRetryCount] = useState<number>(0);
    const containerRef = useRef<HTMLDivElement | null>(null);
    const instanceRef = useRef<UnityInstance>();

    useEffect(() => {
        if (!window.createUnityInstance) {
            const t = window.setTimeout(() => {
                setRetryCount((c) => c + 1);
            }, 100);
            return () => {
                window.clearTimeout(t);
            };
        }
        handleStart();
        return () => {
            const { current } = instanceRef;
            if (current) {
                current.Quit();
            }
        };
    }, [retryCount, renderCount]);

    // キャンバスサイズ調整
    const resizeCanvas = (canvasElement: HTMLCanvasElement | null | undefined) => {
        console.log("resizeCanvas!");
        if (canvasElement) {
            const aspectRatio = 9 / 16;
            const canvasWidth = window.innerWidth * 0.9;
            const canvasHeight = canvasWidth * aspectRatio;
            canvasElement.style.width = canvasWidth.toString() + "px";
            canvasElement.style.height = canvasHeight.toString() + "px";
            console.log(`canvas width = ${canvasElement.style.width}, height = ${canvasElement.style.height}`);
        }
    };

    const onProgress = (n: number) => {
        console.log("createUnityInstance onProgress: ", n);
    };

    const handleStart = () => {
        const { current } = containerRef;
        if (!current) {
            return;
        }

        current.innerHTML = "";
        canvas.setAttribute("id", `unity-canvas`);
        current.appendChild(canvas);

        window
            .createUnityInstance(
                canvas,
                {
                    companyName: "DefaultCompany",
                    productName: "telexistence_test_avatar",
                    productVersion: "0.1",
                    dataUrl: `${unityBuildRoot}/${unityBinaryData.dataUrl}`,
                    frameworkUrl: `${unityBuildRoot}/${unityBinaryData.frameworkUrl}`,
                    codeUrl: `${unityBuildRoot}/${unityBinaryData.codeUrl}`,
                    streamingAssetsUrl: "StreamingAssets",
                },
                onProgress
            )
            .then((instance) => {
                instanceRef.current = instance;
                resizeCanvas(canvas);
            })
            .catch((msg) => {
                console.error(msg);
            });
    };

    return {
        instanceRef,
        containerRef,
    };
};
